
.Advisors {
    overflow: hidden;
    background-image: url(../../media/bg_features2.jpg);
    background-position: center right;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    &Title{
        width: 26%;
        margin: auto;
        margin-bottom: 4%;
    }
    &Wrapper {
        display: grid;
        gap: 10%;
        grid-template-columns: repeat(auto-fit, minmax(calc(360px - 15%), calc(400px - 15%)));
        justify-content: center;
        margin-bottom: 100px;
        &Item {
            text-align: center;
            &Bottom {
                margin-top: 2rem;
                & a {
                    text-decoration: none;
                    font-size: 2rem;
                    transition: all .5s ease;
                    color: var(--color_text_white);
                    &:hover {
                        color: var(--color_primary);
                    }
                }
                & p {
                    color: var(--color_primary);
                }
                width: 120%;
                margin-left: -10%;
            }
        }
    }
}
@media screen and (max-width: 685px) {
    
    .Advisors {
        &Wrapper {
            gap: 2%;
        }
    }
}